.hero-block {
  position: relative;
  background-image: url("../img/divider.svg"), url("../img/back-mobile.jpg");
  background-size: auto, auto;
  background-repeat: no-repeat, no-repeat;
  background-position: bottom, center;
  line-height: 0;
  padding: 90px 19px 85px 19px;
}

.hero-block--media {
  .hero-block;
  background-image: url("../img/divider.svg"), url(../img/back-desktop.jpg);
  line-height: 0;
  padding: 52px 46px 67px 46px;
}

